<script>
export default {
  name: "showResultBG",
  data() {
    return {
      boys: JSON.parse(sessionStorage.getItem("boys")),
      girls: JSON.parse(sessionStorage.getItem("girls")),
      boysAngel: JSON.parse(sessionStorage.getItem("boysAngel")),
      girlsAngel: JSON.parse(sessionStorage.getItem("girlsAngel")),
      sum: sessionStorage.getItem("currentSum"),
      boysCount: sessionStorage.getItem("currentBoy"),
      girlsCount: sessionStorage.getItem("currentGirl")
    }
  },
  methods: {
    findMyAngel(index, gender) {
      if (gender == 1) {
        sessionStorage.setItem("currentAngel", this.boysAngel[index]);
      } else {
        sessionStorage.setItem("currentAngel", this.girlsAngel[index]);
      }
      this.$router.push("/privateInfoBG");
    }
  }
}
</script>

